.cart-icon-menu {
  position: fixed;
  width: 300px;
  height: 400px;
  top: 82px;
  right: 10px;
  background: white;
  border: 1px solid black;
  color: chocolate;
  z-index: 100;
  animation: dropMe 0.1s ease;

  p {
    margin: auto;
  }

  .go-to-cart {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    border: 1px solid black;
    background: none;
    padding: 5px 40px;
    text-decoration: none;
  }
  ul {
    border: 1px solid chocolate;
    box-shadow: inset 0px 0px 5px orange;
    margin: auto;
    overflow-y: scroll;
    padding: 5px;
    width: 95%;
    height: 70%;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    .item {
      display: flex;
      flex-direction: row;
      padding: 5px;
      width: 100%;
      // overflow:hidden;

      // margin:5px auto;
      img {
        height: 40px;
      }
      .quantity {
        width: 20%;
      }
      .wrapper {
        width: 80%;

        // border:1px solid black;

        text-align: left;
      }
    }
  }
}

@keyframes dropMe {
  from {
    top: 75px;
  }
  to {
    top: 82px;
  }
}
