.homepage-header {
  background: url("../../assets/pexels-donald-tong-189333.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  overflow: hidden;
  z-index: 1;
  height: 100vh;
  background-position-y: center;

  .custtitle {
    text-align: end;
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translate(0%, -50%);
    h1 {
      font-size: 10vw;
    }
    h2 {
      font-size: 4vw;
    }
  }
}

.homepage-header::before {
  content: "";
  overflow: hidden;
  width: 100%;
  height: 100vh;
  background: rgb(24, 24, 24);
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.5;
  z-index: -1;
}
