.full-menu {
  width: 80%;
  .horizontal-menu {
    display: inline;
    line-height: 40px;
    width: 100%;
    padding-right: 2rem;
    position: fixed;
    // z-index:10;
    right: 0;
    top: 0;

    li {
      float: right;
      color: white;
      font-size: 1rem;
      min-width: 75px;
      text-align: center;
      display: inline-block;
      margin: 10px 5px;
      transition: 0.2s;
      text-transform: uppercase;
      padding: 0px 10px;
      border-radius: 5px 5px 15px 15px;
      letter-spacing: 1px;
    }
    .item {
      border: 1px solid orange;
    }
    .item:hover {
      cursor: pointer;
      background: white;
      color: orange;
      padding-bottom: 10px;
      padding-top: 10px;
    }
  }
  #chk {
    display: none;
    z-index: -3;
  }
  .close-icon {
    display: none;
    cursor: pointer;
    font-size: 1.5rem;
  }
  .menu-icon {
    display: none;
    float: right;
    font-size: 1.5rem;
    // background: orange;
    p {
      border: 1000px solid black;
    }
  }
}
@media (max-width: 800px) {
  .full-menu {
    .horizontal-menu {
      // display: none;
      z-index: 100;
      max-height: 0;
      max-width: 0;
      width: 0%;
      right: 0;
      transition: max-width 0.2s ease;
      .user {
        display: flex;
        flex-direction: column-reverse;
        word-break: break-all;
      }

      li {
        display: none;
      }
    }
    .menu-icon {
      display: block;
      height: 100%;
      right: 20px;
      top: 10px;
      z-index: 1;
      position: absolute;
      text-align: right;
      cursor: pointer;
    }
    #chk:checked {
      ~ .horizontal-menu {
        box-shadow: 0px 5px 5px 1px black;
        margin-top: 60px;
        z-index: 1;
        display: flex;
        flex-direction: column-reverse;
        align-items: flex-start;
        justify-content: flex-end;
        height: 100%;
        width: 50%;
        max-height: 110%;
        max-width: 110%;
        background: orange;
        li {
          display: block;
          width: auto;
          float: none;
          text-align: center;
          margin-top: 10px;
        }
      }

      ~ .close-icon {
        display: block;
        position: absolute;
        z-index: 2;
        top: 10px;
        height: fit-content;
        right: 20px;
      }

      ~ .menu-icon {
        float: right;
        position: absolute;
        right: 20px;
        top: 10px;

        height: fit-content;
        z-index: 1;
      }
    }
  }
}
