.msgPopup {
  border: 2px solid black;
  position: fixed;
  font-weight: 700;
  background: white;
  z-index: 50;
  color: chocolate;
  // padding:10px;
  border-radius: 2%;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  animation: drop 0.2s ease;
  .close-icon {
    display: block;
    background: none;
    border: none;
    font-size: 1.5rem;
    position: absolute;
    top: 5%;
    right: 5%;
    transform: translate(-2%, -2%);
  }
  .content {
    min-height: 150px;
    width: 60vw;
    max-width: 400px;
    // border-top:2px solid chocolate;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    border-top: 2px solid;
    // overflow:hidden;
    img {
      width: 100%;
      height: 300px;
    }
    .icon {
      padding: 5px;
    }
    .msg {
      overflow: hidden;
      word-break: break-all;
      color: chocolate;
      padding: 1rem;
    }
  }

  .error {
    border-color: hsl(7, 75%, 47%);
    color: hsl(7, 75%, 47%);
  }
  .success {
    border-color: hsl(123, 74%, 27%);
    color: hsl(123, 74%, 27%);
  }
}

@keyframes drop {
  from {
    top: 0%;
  }
  to {
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
