.orange {
  background-color: orange;
  transition: 0.2s;
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.7);
  opacity: 0.9;
}

.transparent {
  background-color: transparent;
  opacity: 0.9;

  .logo {
    color: white;
  }
}

.cus-navbar {
  top: 0;
  position: fixed;
  z-index: 9999;
  width: calc(100vw - (100vw - 100%));
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  overflow: hidden;
  height: 60px;
  max-width: 100vw;
  overflow: hidden;
  // background-color: orange;
}

.logo {
  z-index: 99999999;
  display: inline;
  padding-top: 1%;
  padding-left: 5%;
  :hover {
    cursor: pointer;
  }
}

@media (max-width: 800px) {
  .transparent {
    background: orange;
  }
}
