#tooltip {
  z-index: 0;
}

.textField {
  width: 100%;
  min-width: 200px;
  max-width: 600px;
  input {
    width: 100%;
    height: 2.5rem;
    border-radius: 45px;
    padding: 0px 30px;
    border: 1px solid black;
    outline: none;
    font-size: 1rem;
    letter-spacing: 2px;
  }
  label {
    padding-left: 10px;
    color: black;
    font-size: 1rem;
    font-weight: 300;
    text-transform: capitalize;
    display: block;
    width: 100%;
  }
  input:focus {
    border: 1px solid orange;
  }
}

@media screen and (max-width: 600px) {
  .textField {
    // background: chocolate;
    width: 90vw;
    min-width: 100px;
  }
}
